import React, { useState } from "react";
import Header from "./components/Header";
import Section from "./components/Section";
import Footer from "./components/Footer";
import "./styles/App.css";

import { sectionContent, useLanguageStatus } from "./utils/variables";

function App() {
  const [active, setActive] = useLanguageStatus(false);
  function toggleLanguage() {
    setActive(!active);
  }

  const sections = [0, 1, 2, 3].map((i) => (
    <Section
      isRepo={sectionContent.link[i] === "" ? false : true}
      title={sectionContent.title[i]}
      firstParagraph={
        active
          ? sectionContent.korFirstParagraph[i]
          : sectionContent.firstParagraph[i]
      }
      secondParagraph={
        active
          ? sectionContent.korSecondParagraph[i]
          : sectionContent.secondParagraph[i]
      }
      thirdParagraph={
        active
          ? sectionContent.korThirdParagraph[i]
          : sectionContent.thirdParagraph[i]
      }
      linkToRepo={`https://github.com/tac0de/${sectionContent.link[i]}`}
      linkToSite={`https://${sectionContent.link[i]}.com`}
    />
  ));

  return (
    <div>
      <Header active={active} toggleLanguage={toggleLanguage} />
      {sections}
      <Footer />
    </div>
  );
}

export default App;
